@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #0B090A;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root {
  --primary-white: #fff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52,58,64);
  --primary-black: #000000;
  --primary-transparent-black: rgb(0,0,0,0.7);
  --primary-red: #db0000;
  --primary-hover-red: #f33501;
  --primary-carnelian-red: #BA181B;
}

body{
  font-family: "Roboto Condesed", sans-serif !important;
}
/************** Navbar **************/
nav{
  z-index: 2;
}
.logo{
  width: 16.25rem;
}

/************** Menu Links **************/

nav a.nav-link {
  font-size: 0.8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;

}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;

}

.navbar-brand{
  max-width: 17.25rem !important;
  padding: 0 !important;
  height: 6rem !important;
}

@media(max-width:768px){
  .navbar-brand{
    width: 7rem;
  }
  }


.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #db0000;
  border-top: 0.1875rem solid var(--primary-red);
}

.navbar-nav > li > a:hover{
  border-top: 0.1875rem solid #db0000;
  border-top: 0.1875rem solid var(--primary-red);
}

/************** header **************/

.header-wraper{
  position: relative;
  background: url(data:image/jpeg;base64,) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info{
  display: flex;
  position: absolute;
  top: 0;
  left:0;
  right:0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
color: #db0000;
color: var(--primary-red);
text-transform: uppercase;
}

@media(max-width:768px){
  .main-info h1{
    font-size: 1.7rem !important;
  }
}

/************** Typed Text **************/

.typed-text{
  font-size: 2rem;
  color: white;
}

@media(max-width:768px){
  .typed-text{
    font-size: 1.3rem !important;
  }
}


/************** Header Main offer button **************/

.btn-main-offer{
  border: 1px solid #db0000;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #fff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

/************** Particles JS **************/

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media(max-width: 768px) {
  .particles-canvas{
    display: none;
  }
}

/************** Profile Photo **************/

.photo-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img{
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid #db0000;
  border: 1px solid var(--primary-red);
}

.about-heading {
  color: #db0000;
  color: var(--primary-red);
  text-transform: uppercase;
}

@media(max-width: 468px) {
  .profile-img{
    width: 10rem;
  }
}

/************** Services **************/

.services{
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);

}

.services h1{
  color: rgb(52,58,64);
  color: var(--primary-dark);
  text-transform: uppercase;
}

.services .circle{
  position: relative;
  margin: 0.375rem auto;
  background: #db0000;
  background: var(--primary-red);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon{
  font-size: 1.4rem;
  color: #fff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50% , -50%);

}

.services .box{
  height: 16rem;
  border-bottom: 0.3125rem solid #db0000;
  border-bottom: 0.3125rem solid var(--primary-red);
  padding: 0.625rem;
  margin-bottom:1.875rem;
  background: #fff;
  background: var(--primary-white);
  transition: 0.2s ease-in-out;
}

.services .box:hover{
  background: #db0000;
  background: var(--primary-red);
  border-bottom: 0.3125rem solid rgb(52,58,64);
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.services .box:hover .icon {
  color: rgb(52,58,64);
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: #fff;
  background: var(--primary-white);
}

.services .box:hover p {
  color: #fff;
  color: var(--primary-white);
}

/************** Experience **************/

.experience{
  padding: 0.75rem;
}
.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #db0000;
  color: var(--primary-red);
}

.experience-wrapper::before{
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block{
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
  padding: 0.0625rem;
}

.timeline-block-right{
  float: right;
}

.timeline-block-left{
  float: left;
  direction: rtl;
}

.marker{
  width: 1rem;
  height: 1rem;
  border-radius: 50%;;
  border:0.3125rem solid rgb(52,58,64);;
  border:0.3125rem solid var(--primary-dark);
  background: #db0000;
  background: var(--primary-red);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3{
  margin-top:0.3125rem;
  color: #db0000;
  color: var(--primary-red);
  font-weight: 900;

}


@media(max-width:768px){

  .experience .container{
    padding-left: 0;
  }
  .experience-wrapper::before{
    left:0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right{
    float:none;
    direction: ltr;
    width: 100%;
  }
  .timeline-block-left{
    float:none;
    direction: ltr;
    width: 100%;
  }
}

  /************** portfolio **************/

  .portfolio-wrapper{
    background: #f4f4f4;
    background: var(--primary-light-grey);
    padding: 3rem 0;
  }

  .portfolio-image {
    width: 15rem;
    border:1px solid #db0000;
    border:1px solid var(--primary-red);
    padding: 0 0.5rem;
  }

  .portfolio-image-box {
    position: relative;
    margin: 1rem;

  }

  /************** Testimonials **************/

  .testimonials {
    background-image: url(/-Users-waleed-Developer-Web-Dev-Portfolio-my-app/static/media/testimonials-bg.75612be7.jpg);
    background-size: cover;
    background-color: black;
    text-align: center;
    padding-bottom: 4rem;
    margin-bottom: 0;
    width: 100%;
  }

.testimonials h1 {
  color: #db0000;
  color: var(--primary-red);
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonials-content{
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: rgb(0,0,0,0.7);
  background: var(--primary-transparent-black);
}

.myCarousel{
  color: #fff;
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width:54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;
}

.myCarousel h3{
  color: #db0000;
  color: var(--primary-red);
  font-weight: 100;
  text-transform:uppercase;
  font-family: "Roboto Condesed", sans-serif !important;

}

.myCarousel p{
  color: #ccd1d9;
  color: var(--primary-grey);
}

.carousel .slide img {
  width: 10rem !important;
  border-radius: 50%
}

.carousel .slide{
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;
  height: 100% !important;
}

/************** Footer Components **************/

.footer{
  background: black;
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color:#db0000;
  color:var(--primary-red);
  cursor:pointer;
}

.footer p{
  margin-bottom: 0;
}

